interface btnParam {
    name: string;
    width: number;
    // callback: () => {};
    callback(): void;
};

export const getBtn = (p: btnParam): HTMLElement => {
    let btn = document.createElement('div');
    btn.style.height = '40px';
    btn.style.width = `${p.width}px`;
    btn.style.border = '0px';
    btn.style.padding = '15px';
    btn.style.cssFloat = 'left';
    btn.style.color = '#000000';
    btn.textContent = p.name
    btn.onclick = p.callback;
    btn.onmouseenter = () => {
        btn.style.color = '#fafafa';
    };
    btn.onmouseleave = () => {
        btn.style.color = '#000000';
    };
    return btn;
};